import { ILog } from "./ILog";
import { IGuestInfo, ISystemInfo, IUserInfo } from "./IUserInfo";
type OmitTyped<T, K extends keyof T> = Pick<T, Exclude<keyof T, K>>;
type Primitive = string | Function | number | boolean | Symbol | undefined | null;
type DeepOmit<T, K> = T extends Primitive ? T : { [P in Exclude<keyof T, K>]: DeepOmit<T [P], K> } & {};


export interface iuserinfo extends OmitTyped<IUserInfo, "sessionId"> { sessionId: { inHeader: true, decoded: boolean } | string } 

export interface ILogRequest extends OmitTyped<OmitTyped<ILog, "userInfo">, "correlationId"> {
    correlationId: string | { inHeader: true },
    userInfo: iuserinfo | IGuestInfo | ISystemInfo
}





